* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    font-family: 'Georgia', serif;
    line-height: 1.7;
    background-image: url('https://res.cloudinary.com/dfk8mcpwf/image/upload/v1727496970/OIP_dykvm9.jpg'); 
    background-size: cover; 
    background-position: center; 
    background-repeat: no-repeat; 
    color: #000000;
    margin: 0;
    font-size: 18px;
}

header {
    display: flex;
    justify-content: center; 
    align-items: center; 
    height: 100px;                
}

.logo {
    font-size: 36px;
    font-weight: bold;
    color: #000000;
    letter-spacing: 2px;
}

h1 {
    font-size: 36px;    
    justify-content: center;          
    font-weight: bold;            
}

#toggle-mode {
    background-color: #007bff;
    color: white;
    padding: 10px;
    border: none;
    cursor: pointer;
    border-radius: 4px;
    margin-right:40px;
    font-size: 16px;
}

nav {
    display: flex; 
    justify-content: flex-end; 
    padding: 10px 20px; 

}

nav ul {
    list-style: none; 
    display: flex; 
    margin: 0; 
}

nav li {
    margin: 0 15px; 
}

nav a {
    text-decoration: none; 
    color: #fffafa; 
    font-weight: bold; 
    transition: color 0.3s; 
}

nav a:hover {
    color: #0c0c0c; 
}

#toggle-mode:hover {
    background-color: #0056b3;
}

.content-container,
.article-container,
.sidebar-container {
    transition: transform 0.3s ease; 
}


.content-container:hover,
.article-container:hover,
.sidebar-container:hover {
    transform: scale(1.05); 
}

.main-container {
    display: flex;
    flex-wrap: wrap;
    width: 85%;
    margin: 0 auto;
    max-width: 1200px;
    padding-top: 60px; 
    align-items: stretch; 
}

.content-container {
    flex: 1;  
    min-width: 300px; 
    max-width: 400px; 
    margin-right: 20px; 
}

.article-container {
    flex: 2; 
    padding: 20px;
    background-color: #ffffff00;
    border: 1px solid #eaeaea00;
    margin-right: 20px;
    min-width: 300px; 
    max-width: 800px; 
    min-height: 400px; 
}

.content-container {
    flex: 1;  
    min-width: 300px; 
    max-width: 400px; 
    margin-right: 20px; 
    display: flex; 
    flex-direction: column; 
}

.sidebar-container {
    flex: 1; 
    background-color: #f9f9f900;
    padding: 20px;
    border-left: 1px solid #eaeaea00;
    min-width: 300px; 
    max-width: 400px;
    display: flex; 
    flex-direction: column; 
}

@media (max-width: 768px) {
    .main-container {
        flex-direction: column;
        padding: 10px;
    }

    .content-container, .article-container, .sidebar-container {
        flex: 1;
        margin-right: 0;
        margin-bottom: 20px;
    }
}

article {
    background-color: #f5f5f5;
    padding: 20px;
    margin-bottom: 30px;
    border-bottom: 1px solid #ddd;
}

article img {
    width: 100%;
    height: auto;
    margin-bottom: 15px;
    border-radius: 5px;
}

article h2 {
    font-size: 30px;
    margin-bottom: 10px;
    color: #111;
    font-weight: bold;
}

article p {
    font-size: 18px;
    margin-bottom: 15px;
}

article .read-more {
    font-size: 16px;
    color: #333;
    text-decoration: none;
    border-bottom: 1px solid #333;
    transition: all 0.3s ease;
}

article .read-more:hover {
    color: #555;
    border-bottom: 1px solid #555;
}

#pagination {
    text-align: center;
    margin-top: 20px;
}

#pagination a {
    text-decoration: none;
    color: #333;
    margin: 0 10px;
    font-size: 18px;
}

#pagination a:hover {
    color: #555;
}

@media (max-width: 1024px) {
    .main-container {
        flex-direction: column;
        padding: 10px;
    }

    .content-container, .article-container, .sidebar-container {
        flex: 1;
        margin-right: 0;
        margin-bottom: 20px;
    }
}

footer {
    padding: 20px 0;
    text-align: center;
    font-size: 16px;
    border-top: 1px solid #eaeaea;
    width: 100%; 
    position: relative; 
    bottom: 0; 
}

footer p {
    color: #030303;
    margin: 0; 
}