* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  color: #000;
  background-image: url("https://res.cloudinary.com/dfk8mcpwf/image/upload/v1727496970/OIP_dykvm9.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  margin: 0;
  font-family: Georgia, serif;
  font-size: 18px;
  line-height: 1.7;
}

header {
  justify-content: center;
  align-items: center;
  height: 100px;
  display: flex;
}

.logo {
  color: #000;
  letter-spacing: 2px;
  font-size: 36px;
  font-weight: bold;
}

h1 {
  justify-content: center;
  font-size: 36px;
  font-weight: bold;
}

#toggle-mode {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 4px;
  margin-right: 40px;
  padding: 10px;
  font-size: 16px;
}

nav {
  justify-content: flex-end;
  padding: 10px 20px;
  display: flex;
}

nav ul {
  margin: 0;
  list-style: none;
  display: flex;
}

nav li {
  margin: 0 15px;
}

nav a {
  color: snow;
  font-weight: bold;
  text-decoration: none;
  transition: color .3s;
}

nav a:hover {
  color: #0c0c0c;
}

#toggle-mode:hover {
  background-color: #0056b3;
}

.content-container, .article-container, .sidebar-container {
  transition: transform .3s;
}

.content-container:hover, .article-container:hover, .sidebar-container:hover {
  transform: scale(1.05);
}

.main-container {
  flex-wrap: wrap;
  align-items: stretch;
  width: 85%;
  max-width: 1200px;
  margin: 0 auto;
  padding-top: 60px;
  display: flex;
}

.content-container {
  flex: 1;
  min-width: 300px;
  max-width: 400px;
  margin-right: 20px;
}

.article-container {
  background-color: #fff0;
  border: 1px solid #eaeaea00;
  flex: 2;
  min-width: 300px;
  max-width: 800px;
  min-height: 400px;
  margin-right: 20px;
  padding: 20px;
}

.content-container {
  flex-direction: column;
  flex: 1;
  min-width: 300px;
  max-width: 400px;
  margin-right: 20px;
  display: flex;
}

.sidebar-container {
  background-color: #f9f9f900;
  border-left: 1px solid #eaeaea00;
  flex-direction: column;
  flex: 1;
  min-width: 300px;
  max-width: 400px;
  padding: 20px;
  display: flex;
}

@media (width <= 768px) {
  .main-container {
    flex-direction: column;
    padding: 10px;
  }

  .content-container, .article-container, .sidebar-container {
    flex: 1;
    margin-bottom: 20px;
    margin-right: 0;
  }
}

article {
  background-color: #f5f5f5;
  border-bottom: 1px solid #ddd;
  margin-bottom: 30px;
  padding: 20px;
}

article img {
  border-radius: 5px;
  width: 100%;
  height: auto;
  margin-bottom: 15px;
}

article h2 {
  color: #111;
  margin-bottom: 10px;
  font-size: 30px;
  font-weight: bold;
}

article p {
  margin-bottom: 15px;
  font-size: 18px;
}

article .read-more {
  color: #333;
  border-bottom: 1px solid #333;
  font-size: 16px;
  text-decoration: none;
  transition: all .3s;
}

article .read-more:hover {
  color: #555;
  border-bottom: 1px solid #555;
}

#pagination {
  text-align: center;
  margin-top: 20px;
}

#pagination a {
  color: #333;
  margin: 0 10px;
  font-size: 18px;
  text-decoration: none;
}

#pagination a:hover {
  color: #555;
}

@media (width <= 1024px) {
  .main-container {
    flex-direction: column;
    padding: 10px;
  }

  .content-container, .article-container, .sidebar-container {
    flex: 1;
    margin-bottom: 20px;
    margin-right: 0;
  }
}

footer {
  text-align: center;
  border-top: 1px solid #eaeaea;
  width: 100%;
  padding: 20px 0;
  font-size: 16px;
  position: relative;
  bottom: 0;
}

footer p {
  color: #030303;
  margin: 0;
}
